import axios from 'axios';
import { getBaseEndPoint } from '../../helper/util';
export async function submitSignup(email, pw) {
    let result;
    try {
        result = await axios.post(getBaseEndPoint() + '/users/signup',
            {
                email: email,
                pw: pw,
            }, { withCredentials: true });
        console.log('normal');
        console.log(result.status);
        // get the new user cookie
        // // console.log('success cookie');
        // // console.log(JSON.stringify(result.headers));
        // // console.log('docum');
        // console.log(JSON.stringify(document.cookie));
        //cookie already auto set
        return {
            success: true,
            email: email,
        };
    } catch (e) {
        console.log('some stuff detected');

        console.log(e.response.status);
        if (e.response.status == 409) {
            console.log('user already exist');
            return {
                success: false,
                err: 'user already exist',

            };
        } else { // other code means error
            console.log('some server error, try again alter');
            return {
                success: false,
                err: 'Try again later',

            };
        }
        // if it is 409 means user exist
        //otherwise try again later
    }


}