import VpnKeyIcon from '@material-ui/icons/VpnKey';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import { useEffect } from 'react';
import Cookies from 'js-cookie';
import Login from '../../component/oldLogin';
import { logoutAsync, hide, loadUserFromCookie } from '../login/LoginSlice';
import { useDispatch } from 'react-redux';
import {
    Link,
    useLocation
} from "react-router-dom";

const useStyles = makeStyles((theme) => ({
    header: {
        paddingLeft: "16px",
        paddingRight: "16px",
        background: "white",
        // position: "fixed",
        zIndex: 1,
        top: "0px",
        display: "flex",
        paddingTop: "16px",
        width: "100%",
        justifyContent: "space-between",
        [theme.breakpoints.up('sm')]: {
            paddingLeft: "32px",
            paddingRight: "32px",
            paddingTop: "16px",
        },
        [theme.breakpoints.up('lg')]: {
            paddingLeft: "128px",
            paddingRight: "128px",
            paddingTop: "32px",
        },
    },
    logo: {
        width: "40px",
        height: "40px",
        [theme.breakpoints.up('lg')]: {
            width: "48px",
            height: "48px",

        },
    },
    makeItSquare: {
        alignSelf: 'center',
    }
}));

function NavBar({ isLoggedOn, userEmail, width, handleClickOpen }) {
    // depending on the login state
    const classes = useStyles();
    const theme = useTheme();
    window.aa = theme; //type aa to see the theme

    let location = useLocation();
    let isAtSignUpPage = location.pathname === "/pagesavepublicsite/signup";
    console.log('location');
    console.log(location);
    let dispatch = useDispatch();
    const [anchorEl, setAnchorEl] = React.useState(null);

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    useEffect(() => {
        dispatch(loadUserFromCookie({
            email: Cookies.get('email'),
        }));
    }, []);

    const handleLogout = async (event) => {
        console.log('running logout');
        handleClose(); // close the old logout bubble menu
        handleClickOpen(event); //i think this for log out animatino
        await dispatch(logoutAsync());
        await dispatch(hide());
    }


    return (
        <header className={classes.header} >
            <div style={{
                display: "flex",
                alignItems: "center",
            }}>
                <img src={logo} className={classes.logo} />
                <Link to="/pagesavepublicsite/">
                    <h5 style={{ marginLeft: "8px" }}>Page saver</h5>
                </Link>
            </div>

            <Hidden xsDown>
                {!isLoggedOn ? !isAtSignUpPage && (
                    <div style={{ display: "flex", alignItems: "center" }}>
                        <Button onClick={handleClickOpen} >Login</Button>
                        <Button><Link to="/pagesavepublicsite/signup">Signup</Link></Button>
                    </div>)
                    : (
                        <div style={{ display: "flex", alignItems: "center" }}>
                            <Typography variant="h5" >
                                {userEmail}
                            </Typography>
                            <Button
                                style={{ marginLeft: "32px" }}
                                onClick={handleLogout}
                            >Log Out</Button>
                        </div>

                    )
                }
            </Hidden>

            <Hidden smUp>
                {isLoggedOn && (
                    <>
                        <IconButton
                            aria-label="expand-more"
                            onClick={handleClick}
                            size='medium'
                            classes={{
                                root: classes.makeItSquare,
                            }}
                        >
                            <ExpandMoreIcon />
                        </IconButton>
                        <Menu
                            id="simple-menu"
                            anchorEl={anchorEl}
                            // keepMounted
                            open={anchorEl !== null}
                            onClose={handleClose}
                        >
                            <MenuItem
                                onClick={handleLogout}
                            >Logout</MenuItem>
                        </Menu>
                    </>
                )}
            </Hidden>
            {/* Current width: {width} */}
        </header>
    )
}

export default withWidth()(NavBar);