import axios from 'axios';
import { getBaseEndPoint } from '../../helper/util';
export async function submitURL(url) {
    console.log('submitting new archive');
    let result;
    try {
        result = await axios.post(getBaseEndPoint() + '/pagesave',
            {
                url: url,
            },
            {
                withCredentials: true,
            }
        );
        console.log(result.status);
        return {
            success: true,
        };
    } catch (e) {
        console.log('submitting new archive fail')
        console.log(e.response.status);
        if (e.response.status == 403) {

        }
    }
}