import React from 'react';
import { Slide } from '@material-ui/core';
import { makeStyles } from '@material-ui/core';
import { useFormik } from 'formik';
import { Typography } from '@material-ui/core';
import { Dialog } from '@material-ui/core';
import * as yup from 'yup';
import TextField from '@material-ui/core/TextField';
import { InputAdornment } from '@material-ui/core';
import MailOutlineIcon from '@material-ui/icons/MailOutline';
import VpnKeyIcon from '@material-ui/icons/VpnKey';
import { Button } from '@material-ui/core';
import { Box } from '@material-ui/core';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import { IconButton } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
    changeLoginDialogAlign: {
        justifyContent: ' flex-end',
        alignItems: 'flex-start',
        // borderRadius: '40px',
        '& .MuiPaper-rounded': {
            borderRadius: '20px',
        },
    },
    inputInternalFieldSetHover: {
        '&:hover .MuiOutlinedInput-notchedOutline': {
            borderColor: 'white',

        },
        '&.MuiOutlinedInput-notchedOutline': {
            borderColor: 'white',

        },
        '&.Mui-error .MuiOutlinedInput-notchedOutline': {
            borderColor: 'orange',
        }
    },
    fieldsetBorder: {
        border: 'solid 1px white',
    },
    roundedBorder: {
        borderRadius: '50px',

    },
    forgetpasswordbtn: {
        color: '#fff',
        marginTop: '16px',
        fontSize: theme.typography.subtitle1.fontSize,
    },
    white: {
        color: '#fff',
    }

}));
const validationSchema = yup.object({
    email: yup
        .string('Enter your email')
        .email('Enter a valid email')
        .required('Email is required'),
    password: yup
        .string('Enter your password')
        .min(8, 'Password should be of minimum 8 characters length')
        .required('Password is required'),
});
const forgetPWValidationSchema = yup.object({
    email: yup
        .string('Enter your email')
        .email('Enter a valid email')
        .required('Email is required'),
});
const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="down" ref={ref} {...props} />;
});

function Login(props) {
    const classes = useStyles();
    const { onClose, selectedValue, open } = props;

    const handleClose = () => {
        onClose(selectedValue);
    };

    const handleListItemClick = (value) => {
        onClose(value);
    };
    const formik = useFormik({
        initialValues: {
            email: '',
            password: '',
        },
        validationSchema: validationSchema,
        onSubmit: (values) => {
            alert(JSON.stringify(values, null, 2));
        },
    });

    const forgetPWFormik = useFormik({
        initialValues: {
            email: 'foobar@example.com',
        },
        validationSchema: forgetPWValidationSchema,
        onSubmit: (values) => {
            alert(JSON.stringify(values, null, 2));
        },
    });

    let isAtLogin = false;
    return (
        <Dialog
            TransitionComponent={Transition}
            onClose={handleClose}
            aria-labelledby="simple-dialog-title"
            classes={{
                scrollPaper: classes.changeLoginDialogAlign,
                root: classes.roundedBorder
            }}
            // open={open}
            open={true}
        >
            {/* start of login first screen */}
            {isAtLogin &&
                < div
                    style={{
                        background: "#4B6A5E",
                        display: "flex",
                        flexDirection: 'column',
                        padding: '48px 24px',
                    }}
                >
                    <Typography variant='h4' align='center' style={{ color: '#fff' }}>Welcome</Typography>
                    <Typography variant='h4' align='center' style={{ color: '#fff' }} >to</Typography>
                    <Typography variant='h4' align='center' style={{ color: '#fff' }} >Pagesaver</Typography>

                    <form onSubmit={formik.handleSubmit} style={{ display: 'flex', marginTop: '24px', flexDirection: 'column', }}>
                        <TextField
                            // fullWidth
                            id="email"
                            name="email"
                            label="Email"
                            value={formik.values.email}
                            onChange={formik.handleChange}
                            error={formik.touched.email && Boolean(formik.errors.email)}
                            helperText={formik.touched.email && formik.errors.email}
                            variant='outlined'
                            FormHelperTextProps={{
                                style: { color: 'orange', }
                            }}
                            InputLabelProps={{
                                style: { color: '#fff', }
                            }}
                            InputProps={{
                                startAdornment: (
                                    <InputAdornment position="start">
                                        <MailOutlineIcon />
                                    </InputAdornment>
                                ),
                                style: {
                                    color: '#fff',
                                },
                                classes: {
                                    root: classes.inputInternalFieldSetHover,
                                    notchedOutline: classes.fieldsetBorder,
                                }
                            }}
                        />
                        <TextField
                            // fullWidth
                            id="password"
                            name="password"
                            label="Password"
                            type="password"
                            value={formik.values.password}
                            onChange={formik.handleChange}
                            error={formik.touched.password && Boolean(formik.errors.password)}
                            helperText={formik.touched.password && formik.errors.password}
                            // color='#ffffff'
                            style={{ marginTop: "16px" }}
                            variant='outlined'
                            InputLabelProps={{
                                style: { color: '#fff', }
                            }}
                            FormHelperTextProps={{
                                style: { color: 'orange', }
                            }}
                            InputProps={{
                                startAdornment: (
                                    <InputAdornment position="start">
                                        <VpnKeyIcon />
                                    </InputAdornment>
                                ),
                                style: {
                                    color: '#fff',
                                },
                                classes: {
                                    root: classes.inputInternalFieldSetHover,
                                    notchedOutline: classes.fieldsetBorder
                                }
                            }}
                        />

                        <Button type="submit" variant='outlined' style={{ marginTop: "24px", color: '#fff', borderRadius: '60px', border: '1px solid #fff' }} >Sign in</Button>
                    </form>

                    <Button
                        className={classes.forgetpasswordbtn}
                    >Forget password</Button>
                </div>
            }
            {/* end of login first screen */}
            <div style={{ position: 'relative' }}>
                <div
                    style={{
                        padding: '24px',
                        background: '#4B6A5E',
                        paddingBottom: '128px',
                        maxWidth: '350px',
                    }}
                    className={classes.tophalf}
                >
                    <IconButton
                        color="secondary"
                        aria-label="back to login"
                        classes={{
                            root: classes.white,
                        }}
                    >
                        <ArrowBackIcon />
                    </IconButton>
                    <Typography
                        align='center'
                        variant='h4'
                        style={{
                            color: '#fff'
                        }}
                    >Forget Your Password?</Typography>
                    <Typography
                        align='center'
                        variant='h5'
                        style={{
                            color: '#fff'
                        }}
                    >Enter your email to reset your password</Typography>
                </div>
                <div
                    style={{
                        padding: '24px',
                        background: '#fff',
                        paddingTop: '128px',
                        paddingBottom: '24px',

                    }}
                    className={classes.bottomhalf}
                >
                    <Typography
                        align='center'
                    >Didnt receive a mail?</Typography>
                    <Typography
                        align='center'
                    >Resend email</Typography>
                </div>
                <div
                    style={{
                        width: '100%',
                        height: '100%',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        position: 'absolute',
                        top: '0px',
                        left: '0px',
                        pointerEvents: 'none',
                    }}

                >
                    <Box
                        boxShadow={3}
                        bgcolor="background.paper"
                        m={1}
                        p={3}
                        style={{
                            width: '80%',
                            borderRadius: '20px',
                        }}>
                        <form onSubmit={forgetPWFormik.handleSubmit}>
                            <TextField
                                fullWidth
                                id="email"
                                name="email"
                                label="Email"
                                value={forgetPWFormik.values.email}
                                onChange={forgetPWFormik.handleChange}
                                inputProps={{
                                    style: {
                                        pointerEvents: 'initial',
                                    }
                                }}
                                error={forgetPWFormik.touched.email && Boolean(forgetPWFormik.errors.email)}
                                helperText={forgetPWFormik.touched.email && forgetPWFormik.errors.email}
                            />
                            <Button
                                color="primary"
                                variant="contained"
                                style={{ marginTop: '16px', pointerEvents: 'initial', }}
                                fullWidth
                                type="submit">
                                reset
                            </Button>
                        </form>
                    </Box>
                </div>
            </div >
        </Dialog >
    );
}
export default Login;