import { createAsyncThunk, createSlice, current } from '@reduxjs/toolkit';
import { submitSignup } from './SignupAPI';

const initialState = {
    signupstatus: 'idle',
    serverDown: false,
};

export const signupAsync = createAsyncThunk(
    'signup/signup',
    async ({ email, pw }, { rejectWithValue }) => {
        // async ({ email, pw }, thunkAPI) => {
        const resp = await submitSignup(email, pw);
        console.log('check here');
        console.log(resp);
        if (!resp.success) {
            console.log('rejecting');
            return rejectWithValue(resp.err);
        }

        console.log('success somehow');
        return resp;

    }
);


export const signupSlice = createSlice({
    name: 'signup',
    initialState,
    reducers: {
        // console.log(current(state));
        // https://redux-toolkit.js.org/api/createReducer#logging-draft-state-values
        resetExistingAlert: (state, action) => {
            state.signupstatus = 'idle'
        },
        resetServerDownAlert: (state, action) => {
            state.serverDown = false;
        },

    },
    extraReducers: {
        [signupAsync.pending]: (state, action) => {
            state.signupstatus = 'pending';
        },
        [signupAsync.rejected]: (state, action) => {
            if (action.payload === 'user already exist')
                state.signupstatus = 'existing';
            else
                state.signupstatus = 'error';
        },
        [signupAsync.fulfilled]: (state, action) => {
            state.signupstatus = 'done';
            // i think i need the other place to listen to this action so that it would redirect to home screen
        }
    }

});

export const selectSignupStatus = (state) => (state.signup.signupstatus);
export const { resetExistingAlert, resetServerDownAlert } = signupSlice.actions;
export default signupSlice.reducer;