import axios from 'axios';
import { getBaseEndPoint } from '../../helper/util';
export async function login(email, pw) {
    console.log('axios submitting:' + email + ' ,' + pw);
    let result;
    try {
        result = await axios.post(getBaseEndPoint() + '/users/login',
            {
                email: email,
                pw: pw,
            },
            {
                withCredentials: true,
            }
        );
        console.log('logging in axios');
        console.log(result.status);
        return {
            success: true,
            email: email,
        };
    } catch (e) {
        console.log('some stuff detected');

        console.log(e.response.status);
        if (e.response.status == 403) {
            console.log('incorrect credentials');
            return {
                success: false,
                err: 'incorrect credentials',

            };
        } else { // other code means error
            console.log('login faileddue to some other');
            return {
                success: false,
                err: 'Try again later',

            };
        }
    }
}

